import React from 'react';
import { useSelector } from 'react-redux';
import Wrap from 'components/wrap';
import { Header2, Description1 } from 'components/typography';
import SectorItem from './item';

import useStyles from './use-styles';

export default function Top() {
	const styles = useStyles();
	const { payload } = useSelector((state) => state.HomeInfo);

	const items = [
		{
			title: 'Sport',
			description: 'Discover Projects',
		},
		{
			title: 'Leisure & Media',
			description: 'Discover Projects',
		},
		{
			title: 'Agency',
			description: 'Discover Projects',
		},
		{
			title: 'Recruitment',
			description: 'Discover Projects',
		},
		{
			title: 'Healthcare',
			description: 'Discover Projects',
		},
	];
	return (
		<section className={styles.domain}>
			<Wrap>
				<Header2
					title={payload && payload.sectorFieldName}
					className={styles.headerOffset}
				/>
				<Description1
					className={styles.descriptionOffset}
					title={payload && payload.ectorFieldDescription}
				/>
				<div className={styles.sectorList}>
					{items.map((item, index) => (
						<SectorItem
							title={item.title}
							description={item.description}
							index={index}
						/>
					))}
				</div>
			</Wrap>
		</section>
	);
}
